const app = getApp()
import helper from '../common/helper.js';
export default {
	data() {
		return {
			imgUrl: '',
			friendsList: []
		}
	},
	onLoad() {
		this.imgUrl = helper.imgUrl
		// 加载好友列表
		this.getFriends()
	},
	methods: {
		getFriends() {
			uni.showLoading({
			  title: '加载中...',
				duration: 1000000
			});
			uni.request({
				url: `${helper.requestUrl}/user/friends`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res)
					console.log(res);
					if (res.data.status_code == '1') {
						this.friendsList = res.data.data
					} else {
						uni.showToast({
							title: res.data.message,
							icon: "none"
						});
					}
				}
			});
		},
	}
}
